export default {
	data() {
		return {
			Router: this.$Router,
			indProDate: [{}, {}, {}, {}],
			swiperData: [],
			mainData: [],
			labelData: {},
			city: '',
			labelTwoData: {},
			userInfoData: {}
		}
	},

	onLoad() {
		const self = this;
		self.paginate = self.$Utils.cloneForm(self.$AssetsConfig.paginate);
		const callback = (res) => {
			self.$Utils.loadAll([ 'getLabelData', 'getLocation', 'getLabelTwoData'], self);
		};
		self.$Token.getProjectToken(callback, {
			refreshToken: true
		})
	},


	onReachBottom() {
		console.log('onReachBottom')
		const self = this;
		if (!self.isLoadAll && uni.getStorageSync('loadAllArray')) {
			self.paginate.currentPage++;
			self.getMainData()
		};
	},

	onPullDownRefresh() {
		console.log('refresh');
		const self = this;
		self.$Utils.loadAll(['getSliderData', 'getLabelData', 'getLocation', 'getLabelTwoData'], self);
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	},

	onShow() {
		const self = this;
		if (uni.getStorageSync('city')) {
			self.mainData = [];
			self.city = uni.getStorageSync('city');
			self.getCityData();
			uni.removeStorageSync('city')
		}
		self.getUserInfoData()
	},

	methods: {

		toTab() {
			const self = this;
			uni.setStorageSync('index', 2);
			self.Router.switchTab({
				route: {
					path: '/pages/fengxiangba/fengxiangba'
				}
			})
		},

		getUserInfoData() {
			const self = this;
			const postData = {};
			postData.tokenFuncName = 'getProjectToken';
			postData.searchItem = {
				user_no: uni.getStorageSync('user_info').user_no
			};
			const callback = (res) => {
				if (res.info.data.length > 0) {
					self.userInfoData = res.info.data[0];
				}
			};
			self.$apis.userInfoGet(postData, callback);
		},

		formIdAdd(e) {
			const self = this;
			console.log(e)

			self.$apis.WxFormIdAdd(e.detail.formId, Date.parse(new Date()) / 1000 + 7 * 86400);
		},

		getLocation() {
			const self = this;
			const callback = (res) => {
				if (res) {
					console.log('res', res)
					if (res.authSetting) {
						console.log(232)
						return
					}
					self.city = res.address_component.city
					self.getCityData()
				};
			};
			self.$Utils.getLocation('reverseGeocoder', callback);

		},

		getCityData() {
			const self = this;
			const postData = {};
			postData.searchItem = {
				thirdapp_id: 2,
				title: self.city
			};
			const callback = (res) => {
				if (res.info.data.length > 0) {
					self.cityData = res.info.data[0];
					self.city_id = self.cityData && self.cityData.id ? self.cityData.id : 24;
					self.getMainData(true)
				} else {
					uni.showModal({
						title: '提示',
						content: '当前城市未开通，是否切换为默认城市西安',
						success: function(res) {
							if (res.confirm) {
								self.city_id = self.cityData && self.cityData.id ? self.cityData.id : 24;
								self.city = '西安市';
								self.getMainData(true)
							} else if (res.cancel) {
								console.log('用户点击取消');
							}
						}
					});
				}

			};
			self.$apis.labelGet(postData, callback);
		},

		getLabelData() {
			const self = this;
			const postData = {};
			postData.searchItem = {
				thirdapp_id: 2,
				title: '首页推荐位'
			};
			const callback = (res) => {
				if (res.info.data.length > 0) {
					self.labelData = res.info.data[0]
				}
				console.log('self.swiperData', self.swiperData)
				self.$Utils.finishFunc('getLabelData');
			};
			self.$apis.labelGet(postData, callback);
		},

		getLabelTwoData() {
			const self = this;
			const postData = {};
			postData.searchItem = {
				thirdapp_id: 2,
				title: '常规团推荐位'
			};
			const callback = (res) => {
				if (res.info.data.length > 0) {
					self.labelTwoData = res.info.data[0]
				}
				console.log('self.swiperData', self.swiperData)
				self.$Utils.finishFunc('getLabelTwoData');
			};
			self.$apis.labelGet(postData, callback);
		},

		intoStore() {
			const self = this;
			if (self.userInfoData.check_status == 0) {
				self.Router.navigateTo({
					route: {
						path: '/pages/myShopApply/myShopApply'
					}
				})
			} else if (self.userInfoData.check_status == 1) {
				self.$Utils.showToast('您的资料审核中', 'none');
			} else if (self.userInfoData.check_status == 2 && self.userInfoData.level >= 3) {
				self.Router.navigateTo({
					route: {
						path: '/pages/myShop/myShop'
					}
				})
			}
		},

		intoNew() {
			const self = this;
			if (self.userInfoData.level == 0) {
				self.Router.navigateTo({
					route: {
						path: '/pages/newPeople/newPeople'
					}
				})
			} else {
				self.$Utils.showToast('您已不是新人身份', 'none');
			}
		},

		getMainData(isNew) {
			const self = this;
			if (isNew) {
				self.mainData = [];
				self.paginate = {
					count: 0,
					currentPage: 1,
					is_page: true,
					pagesize: 5
				}
			};
			const postData = {};
			postData.paginate = self.$Utils.cloneForm(self.paginate);
			postData.searchItem = {
				thirdapp_id: 2,
				type: 1,
				level: ['>', 0],
				category_id: ['not in', [57]],
				area_id: self.city_id
			};
			postData.getAfter = {
				order: {
					token: uni.getStorageSync('user_token'),
					tableName: 'Order',
					middleKey: 'product_no',
					key: 'product_no',
					searchItem: {
						status: 1,
						order_step: 4,
						pay_status: 1
					},
					condition: '='
				}
			};
			const callback = (res) => {
				if (res.info.data.length > 0) {
					self.mainData.push.apply(self.mainData, res.info.data);
				}
				console.log('self.mainData', self.mainData)
				self.$Utils.finishFunc('getLocation');
			};
			self.$apis.productGet(postData, callback);
		},

		getSliderData() {
			const self = this;
			const postData = {};
			postData.searchItem = {
				thirdapp_id: 2,
			};
			postData.getBefore = {
				caseData: {
					tableName: 'Label',
					searchItem: {
						title: ['=', ['首页轮播图']],
					},
					middleKey: 'parentid',
					key: 'id',
					condition: 'in',
				},
			};
			postData.getAfter = {
				Article: {
					tableName: 'Article',
					searchItem: {
						status: 1,
					},
					middleKey: 'id',
					key: 'menu_id',
					condition: 'in',
					info: ['id']
				},
			};
			const callback = (res) => {
				if (res.info.data.length > 0) {
					self.swiperData.push.apply(self.swiperData, res.info.data)
				}
				console.log('self.swiperData', self.swiperData)
				self.$Utils.finishFunc('getSliderData');
			};
			self.$apis.labelGet(postData, callback);
		},
	},

};
